import Fetch from './fetch';
export function getCurrentPosition(pos) {
  let str = '';
  if (typeof pos === 'string') {
    str = `&keyword=${pos}`;
  } else {
    str = `&latlng=${pos.nowLatitude},${pos.nowLongitude}`;
  }
  return Fetch(`/bbs/photo.php?action=location${str}`).then(response => {
    return response.json();
  });
};

const publish = (data) => {
  if (publish.pubStatus) {
    publish.pubStatus = 0;
    return Fetch('/bbs/photo.php?action=post', {method: 'POST', credentials: 'include', body: data, headers: {'content-type': 'application/x-www-form-urlencoded'}}).then(response => {
      publish.pubStatus = 1;
      return response.json();
    });
  } else {
    return new Promise((resolve, reject) => {
      reject(1);
    })
  }
};
publish.pubStatus = 1;
export {publish};

export default function getToken() {
  return Fetch('/bbs/user/qiniutoken', {credentials: 'include'}).then(response => {
    return response.json();
  }).then(res => {
    return new Promise((resolve, reject) => {
      if (res && res.result === 'ok') {
        resolve(res.data.tk);
      } else {
        reject();
      }
    })
  });
}



